.feature__title {
  font-size: 14px;
  font-style: normal;
  line-height: 175%;
}

.features-item__top {
  display: flex;
  align-items: center;
  padding-left: 15px;
  height: 32px;
  width: 166px;
  border-radius: 8px 8px 0px 0px;
  border: 1px solid rgba(55, 62, 83, 0.5);
  background: #06132b;
  box-shadow: 0px 0px 3px 3px rgba(100, 255, 218, 0.2);
}

.feature__content {
  padding: 15px;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: 120%;
  border-radius: 0px 0px 8px 8px;
  border: 1px solid rgba(55, 62, 83, 0.5);
  background: #06132b;
  box-shadow: 0px 3px 3px 3px rgba(100, 255, 218, 0.2);
}

.feature-content__list {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.feauture-content__item {
  display: flex;
  align-items: center;
  gap: 8px;

  &::before {
    content: '';
    display: block;
    width: 8px;
    height: 8px;
    background-image: url('../../assets/icons/arrow-accent-right.svg');
    background-repeat: no-repeat;
    background-size: contain;
  }
}