.header {
  position: sticky;
  top: 0;
  right: 0;
  left: 0;
  padding-block: 30px;
  display: flex;
  justify-content: space-between;
  background-color: $background-color;
  z-index: 10;
}

.header__nav-bar {
  display: flex;
  align-items: center;

}

.header__nav-list {
  display: flex;
  gap: 30px;
}

.heder__nav-link {
  color: $text-primary;
}